import {useDispatch, useSelector} from "react-redux";
import {useEffect, useState} from "react";
import {validator} from "../../Utils/validator";
import TextField from "../../Common/form/textField";
import RadioField from "../../Common/form/radioField";
import CheckBoxField from "../../Common/form/checkBoxField";
import TextAreaField from "../../Common/form/textAreaField";
import {signUp} from "../../Store/users";

export const SignUpForm = () => {
    const dispatch = useDispatch();
    const [data, setData] = useState({
        email: "",
        password: "",
        sex: "male",
        name: "",
        licence: false,
        location: "",
        dateOfBirth: '',
        description: '',
        telephone: ''
    });
    const [errors, setErrors] = useState({});

    const handleChange = (target) => {
        setData((prevState) => ({
            ...prevState,
            [target.name]: target.value
        }));
    };
    const validatorConfig = {
        email: {
            isRequired: {
                message: "    "
            },
            isEmail: {
                message: "Email  "
            }
        },
        location: {
            isRequired: {
                message: "    "
            }
        },
        telephone: {
            isRequired: {
                message: "   "
            }
        },
        dateOfBirth: {
            isRequired: {
                message: "  "
            }
        },
        name: {
            isRequired: {
                message: "   "
            },
            min: {
                message: `     3 `,
                value: 3
            }
        },
        password: {
            isRequired: {
                message: "   "
            },
            isCapitalSymbol: {
                message: "       "
            },
            isContainDigit: {
                message: "      "
            },
            min: {
                message: "     8 ",
                value: 8
            }
        },
        licence: {
            isRequired: {
                message:
                    "         "
            }
        }
    };
    useEffect(() => {
        validate();
    }, [data]);
    const validate = () => {
        const errors = validator(data, validatorConfig);
        setErrors(errors);
        return Object.keys(errors).length === 0;
    };
    const isValid = Object.keys(errors).length === 0;

    const handleSubmit = (e) => {
        e.preventDefault();
        const isValid = validate();
        if (!isValid) return;
        const newData = {
            ...data,
        };
        console.log(newData)
        dispatch(signUp(newData));
    };
    return (
        <form onSubmit={handleSubmit}>
            <TextField
                label=""
                name="name"
                value={data.name}
                onChange={handleChange}
                error={errors.name}
                placeholder={""}
            />
            <TextField
                label=" "
                name="email"
                value={data.email}
                onChange={handleChange}
                error={errors.email}
                placeholder={""}
            />
            <TextField
                label=""
                type="password"
                name="password"
                value={data.password}
                onChange={handleChange}
                error={errors.password}
                placeholder={""}
            />
            <TextField
                label=" "
                type="date"
                name="dateOfBirth"
                value={data.dateOfBirth}
                onChange={handleChange}
                error={errors.dateOfBirth}
                placeholder={".."}
            />
            <TextField
                label=""
                type="tel"
                name="telephone"
                value={data.telephone}
                onChange={handleChange}
                error={errors.telephone}
                placeholder={"+7 (___) __-__"}
            />
            <TextAreaField
                label={" "}
                value={data.location}
                error={errors.location}
                onChange={handleChange}
                name={"location"}
                placeholder={" "}
            />
            <TextAreaField
                label={" "}
                value={data.description}
                onChange={handleChange}
                name={"description"}
                placeholder={" "}
            />
            <RadioField
                options={[
                    { name: "", value: "male" },
                    { name: "", value: "female" },
                    { name: "", value: "other" }
                ]}
                value={data.sex}
                name="sex"
                onChange={handleChange}
                label="  "
            />
            <CheckBoxField
                value={data.licence}
                onChange={handleChange}
                name="licence"
                error={errors.licence}
            >
                 <a> </a>
            </CheckBoxField>
            <button
                className="btn btn-primary w-100 mx-auto"
                type="submit"
                disabled={!isValid}
            >
                
            </button>
        </form>
    );
}